.LevelButtonsWrap{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .Button{
    min-width: 35px;
    cursor: pointer;
    padding: 10px;
    border: 3px solid white;
    border-radius: 15px;
    height: 35px;
    width: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    color: white;
    margin-bottom: 10px;
    &:not(:first-child){
      margin-left: 10px;
    }
  }
}